@import "../../global.scss";

.menu{
    width:300px;
    height:100vh;
    background-color: $mainColor;
    position:fixed;
    top:70px;
    right:-300px;
    z-index:2;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;
    @include mobile{
        width:100px;
    }

    &.active{
        right:0;
    }
    ul{
        margin:0;
        padding:0;
        list-style:none;
        font-size: 30px;
        color:white;
        width:60%;
        @include mobile{
            font-size: 20px;
        }
        
        li{
            margin-bottom: 25px;
            a{
                
                font-size: inherit;
                color:inherit;
                text-decoration: none;
            }
            &:hover{
                font-weight: 600;
            }
        }
    }
}